<style lang="less" scoped>
  .tabBtn{
    padding-left: 30px;
    background-color: #fff;
    padding-top: 26px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    &>button.active{
      background-color: #3eb140;
      color: #fff;
    }
    &>button{
      width: 120px;
      height: 44px;
      border-radius: 20px;
      background-color: #fff;
      color: #464646;
      border: none;
      border: 0;
      outline: none;
      cursor: pointer;
      &:nth-child(2){
        margin-left: 30px;
      }
    }
  }
  .taskDataList{
    width: 100%;
    padding-top: 20px;
    background-color: #fff;
    min-height: 90vh;
    &>div{
      width: 93%;
      margin: 0 auto 20px auto;
      -moz-box-shadow:-1px 9px 20px #B5B5B5;
      -webkit-box-shadow:-1px 9px 20px #B5B5B5;
      box-shadow:-1px 9px 20px #B5B5B5;
      border-radius: 30px;
      &>div:nth-child(1){
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 100px;
        &>p:nth-child(1){
          flex: 2;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          font-size: 30px;
          padding-left: 20px;
        }
        &>p:nth-child(2){
          flex: 5;
          font-size: 30px;
          &>img{
            margin-left: 10px;
          }
        }
        &>p:nth-child(3){
          flex: 3;
          text-align: right;
          &>span{
            color: #fff;
            background-color: #8de5ff;
            width: 112px;
            height: 100px;
            display: inline-block;
            line-height: 100px;
            text-align: center;
            border-radius: 0 30px 30px 0;
          }
          &>img{
            width: 52px;
            height: 52px;
            vertical-align: middle;
            margin-right: 10px;
          }
        }
      }
      &>div:nth-child(2){
        background-color: #f5f5f5;
        padding-left: 10px;
        border-radius: 0 0 30px 30px;
        &>div{
          display: flex;
          justify-content: space-around;
          align-items: center;
          height: 100px;
          &>p:nth-child(1){
            flex: 6;
            overflow-x: hidden;
            overflow-y: auto;
            height: 100px;
            padding-top: 35px;
            font-size: 30px;
          }
          &>p:nth-child(2){
            flex: 4;
            font-size: 26px;
            vertical-align: middle;
            padding-left: 20px;
            color: #969696;
            padding-right: 20px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            &>img{
              width: 35px;
              height: 35px;
              vertical-align: middle;
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
  /* 无列表的情况下，显示背景图片 */
  .bg_img {
      width: 100%;
      text-align: center;
  }
  .bg_img>img{
      margin-top:30px;
      width: 330px;
      height: 316px;
  }
  .bg_img>p{
      margin-top: 20px;
      font-size: 30px;
      color: #464646;
  }
</style>

<template>
  <div class="container">
    <header-nav-back centerTitle="提交作业"></header-nav-back>
    <div class="tabBtn">
      <button @click="changeSubmitStatus('unSubmit')" :class="{active: !submited}">未提交</button>
      <button @click="changeSubmitStatus('submited')" :class="{active: submited}">已提交</button>
    </div>

      <!-- 未提交列表 -->
      <div key="unSubmited" v-if="!submited" class="taskDataList ">
        <!-- <div v-if="courses.unSubmitList && courses.unSubmitList.length > 0 && unItem.isTop === 'Y'" v-for="(unItem, indexUn) in courses.unSubmitList" :key="indexUn">
          <div>
            <p @click="openWorkList(unItem)">{{unItem.courseChannelTitle}}</p>
            <p @click="openWorkList(unItem)">讲师布置{{unItem.total}}份作业<img src="../../assets/img/xiala_icon_tizy.png" alt="openIcon"></p>
            <p v-if="unItem.isTop  === 'Y'"><img src="../../assets/img/weitjiao_zhiding.png" alt="置顶"></p>
          </div>
          <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
            <div class="animated" v-if="unItem.showStatus">
              <div @click="enterWorkRequire(unSubmitDataList)" v-for="(unSubmitDataList, index1) in unItem.taskList" :key="index1">
                <p>{{unSubmitDataList.taskTitle}}</p>
                <p><img src="../../assets/img/huifu_biaoqing.png" alt="闹钟">{{unSubmitDataList.time | datePoint}}</p>
              </div>
            </div>  
          </transition>
        </div> -->

        <div v-if="courses.unSubmitList && courses.unSubmitList.length > 0" v-for="(unItem, index) in courses.unSubmitList" :key="index">
          <div>
            <p  @click="openWorkList(unItem)">{{unItem.courseChannelTitle}}</p>
            <p  @click="openWorkList(unItem)">讲师布置{{unItem.total}}份作业<img :style="!unItem.showStatus ? 'transform: rotate(0deg)' : 'transform: rotate(180deg)'" src="../../assets/img/xiala_icon_tizy.png" alt="openIcon"></p>
            <p v-if="unItem.isTop === 'N' && courses.unSubmitList[0].isTop === 'N'"><span @click="changeTopStatus(unItem, 'unsubmit')">置顶</span></p>
            <p v-if="unItem.isTop === 'N' && courses.unSubmitList[0].isTop === 'Y'"><span :style="{backgroundColor: (unItem.isTop === 'N' && (courses.unSubmitList[0].isTop === 'Y' || courses.submitList[0].isTop === 'Y')) ? '#c8c8c8' : '#8de5ff'}">置顶</span></p>
            <p v-if="unItem.isTop === 'Y'"><img src="../../assets/img/weitjiao_zhiding.png" alt="置顶"><span @click="changeTopStatus(unItem, 'unsubmit')">取消置顶</span></p>
          </div>
          <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
            <div class="animated" v-if="unItem.showStatus">
              <div @click="enterWorkRequire(unSubmitDataList)" v-for="(unSubmitDataList, index_) in unItem.taskList" :key="index_">
                <p>{{unSubmitDataList.taskTitle}}</p>
                <p><img class="lock-time" src="../../assets/img/clock.png" alt="闹钟">{{unSubmitDataList.time | datePoint}}</p>
              </div>
            </div>  
          </transition>
        </div>

        <section class="bg_img" v-if="!courses.unSubmitList || !courses.unSubmitList.length">
          <img :src="noList" alt="没有作业啦">
          <p>讲师还未发布作业哦~</p>
        </section>

      </div>

      <!-- 已提交列表 -->
      <div key="submited" v-if="submited" class="taskDataList ">

        <!-- <div v-if="courses.submitList && courses.submitList.length > 0 && unItem.isTop === 'Y'" v-for="(unItem, index) in courses.submitList" :key="index">
          <div>
            <p  @click="openWorkList(unItem)">{{unItem.courseChannelTitle}}</p>
            <p  @click="openWorkList(unItem)">讲师布置{{unItem.total}}份作业<img src="../../assets/img/xiala_icon_tizy.png" alt="openIcon"></p>
            <p v-if="unItem.isTop === 'N'"><span>取消置顶</span></p>
            <p v-if="unItem.isTop === 'Y'"><img src="../../assets/img/weitjiao_zhiding.png" alt="置顶"></p>
          </div>
          <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
            <div class="animated" v-if="unItem.showStatus">
              <div @click="enterWorkRequire(unSubmitDataList)" v-for="(unSubmitDataList, index) in unItem.taskList" :key="index">
                <p>{{unSubmitDataList.taskTitle}}</p>
                <p><img src="../../assets/img/huifu_biaoqing.png" alt="闹钟">{{unSubmitDataList.time | datePoint}}</p>
              </div>
            </div>  
          </transition>
        </div> -->

        <div v-if="courses.submitList && courses.submitList.length > 0" v-for="(unItem, index) in courses.submitList" :key="index">
          <div>
            <p  @click="openWorkList(unItem)">{{unItem.courseChannelTitle}}</p>
            <p  @click="openWorkList(unItem)">讲师布置{{unItem.total}}份作业<img :style="!unItem.showStatus ? 'transform: rotate(0deg)' : 'transform: rotate(180deg)'" src="../../assets/img/xiala_icon_tizy.png" alt="openIcon"></p>
            <p v-if="(unItem.isTop === 'N' && courses.submitList[0].isTop === 'N' && courses.unSubmitList[0].isTop === 'N')"><span @click="changeTopStatus(unItem, 'unsubmit')">置顶</span></p>
            <p v-if="(unItem.isTop === 'N' && courses.submitList[0].isTop === 'Y' || unItem.isTop === 'N' && courses.unSubmitList[0].isTop === 'Y')"><span :style="{backgroundColor: (unItem.isTop === 'N' && courses.submitList[0].isTop === 'Y' || unItem.isTop === 'N' && courses.unSubmitList[0].isTop === 'Y') ? '#c8c8c8' : '#8de5ff'}">置顶</span></p>
            <p v-if="unItem.isTop === 'Y'"><img src="../../assets/img/weitjiao_zhiding.png" alt="置顶"><span @click="changeTopStatus(unItem, 'unsubmit')">取消置顶</span></p>
          </div>
          <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
            <div class="animated" v-if="unItem.showStatus">
              <div @click="enterWorkRequire(unSubmitDataList)" v-for="(unSubmitDataList, index) in unItem.taskList" :key="index">
                <p>{{unSubmitDataList.taskTitle}}</p>
                <p><img class="lock-time" src="../../assets/img/clock.png" alt="闹钟">{{unSubmitDataList.time | datePoint}}</p>
              </div>
            </div>  
          </transition>
        </div>


        <section class="bg_img" v-if="!courses.submitList || !courses.submitList.length">
          <img :src="noList" alt="没有作业啦">
          <p>讲师还未发布作业哦~</p>
        </section>
      </div>

  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack
    },
    data() {
      return {
        courses: [],
        submited: false,
        noList: require('../../assets/img/kognbaiye_weitijiaozuoye.png')
      }
    },
    methods: {
      // 进入作业要求模块
      enterWorkRequire(item){
        this.$router.push({
          name: 'workRe',
          params: {
            taskId: item.taskId,
            title: item.taskTitle
          }          
        })
      },
      // 置顶操作
      changeTopStatus(item, info){
        let params = {
          courseId: item.courseId,
          courseChannelId: item.courseChannelId,
          op: 'TOPPING'
        }
        if(item.isTop === 'Y'){
          params['op'] = 'CANCEL_TOPPING'
          this.$Indicator.open({
            text: '正在取消置顶...',
            spinnerType: 'fading-circle'
          })
        }else{
          this.$Indicator.open({
            text: '置顶中...',
            spinnerType: 'fading-circle'
          })
        }
        
        this.axios.post('/api/setToppingChannel', params).then(data => {
          if(data.data.status === 'success'){
            
            // 再次请求新的数据
            this.axios.post('/api/courses/getStudentCourseChannels', {
              courseId: item.courseId
            }).then((res) => {
              if (res.data.result !== 'failure') {
                this.courses = res.data.result
              }
              this.$Indicator.close()
              
              if(data.data.result.indexOf('取消') > -1){
                this.$Toast({
                  message: '取消置顶',
                  position: 'middle',
                  duration: 1000
                })
              }else{
                this.$Toast({
                  message: '置顶成功',
                  position: 'middle',
                  duration: 1000
                })
              }
              
            }).catch((err) => {
              console.log(err)
            })

          }else{
            this.$Toast({
							message: data.data.result,
							position: 'middle',
							duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 改变状态
      changeSubmitStatus(item){
        if(item === 'unSubmit'){
          this.submited = false
        }else{
          this.submited = true
        }
      },
      // 手风琴效果
      openWorkList(item){
        if(item.showStatus){
          for (let key in this.courses) {
            if(this.courses[key].length > 0){
              this.courses[key].forEach((element, index) => {
                this.$set(element, 'showStatus', false)
              })
            }
          }
        }else{
          for (let key in this.courses) {
            if(this.courses[key].length > 0){
              this.courses[key].forEach((element, index) => {
                this.$set(element, 'showStatus', false)
              })
            }
          }
          item.showStatus = true
        }
      }
    },
    created() {

      window.onscroll = () => {
        return
      }

      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })

      this.axios.post('/api/courses/getStudentCourseChannels', {
        courseId: this.$route.params.courseId
      }).then((res) => {
        if (res.data.result !== 'failure') {
          this.courses = res.data.result
        }
        this.$Indicator.close()
      }).catch((err) => {
        console.log(err)
      })
    }
  }
</script>



